
body {
  font-family: sans-serif;
  margin: 0;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  //background: aqua;
}

.screen {
  //width: calc(108px*1);
  //height: calc(192px*1);
  width: 108px;
  height: 192px;
  background: black;
  //border-radius: 2px;
  overflow: hidden;
}

.line{
  display: flex;
  flex-direction: row;
  width: fit-content;
}

.words {
  //padding-left: 2%;
  padding-left: 10px;
  white-space: nowrap;
  width: fit-content;
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke: 1px rgb(255, 255, 255);
  font-weight: 900;
  font-family: Roboto;
}

.solid {
  color: white;
}

.wrapper {
  background: rgb(255, 137, 137);
}